// Project Tasks API v1
// GET /api/v1/projects/[projectId]/tasks - List tasks for a project
// POST /api/v1/projects/[projectId]/tasks - Create task in project

import { NextRequest, NextResponse } from 'next/server'
import { supabaseAdmin } from '@/lib/supabase/server'
import type { TaskStatus, TaskPriority, TaskType } from '@/lib/project-management/types'

interface RouteParams {
  params: Promise<{ projectId: string }>
}

// GET - List tasks for a project
export async function GET(request: NextRequest, { params }: RouteParams) {
  try {
    const { projectId } = await params
    const { searchParams } = new URL(request.url)
    
    // Query parameters
    const search = searchParams.get('search') || ''
    const status = searchParams.get('status') as TaskStatus | null
    const priority = searchParams.get('priority') as TaskPriority | null
    const assigneeId = searchParams.get('assigneeId')
    const type = searchParams.get('type') as TaskType | null
    const tags = searchParams.get('tags')?.split(',').filter(Boolean) || []
    const page = parseInt(searchParams.get('page') || '1')
    const limit = parseInt(searchParams.get('limit') || '50')
    const sortBy = searchParams.get('sortBy') || 'position'
    const sortOrder = searchParams.get('sortOrder') || 'asc'
    const groupBy = searchParams.get('groupBy') // 'status', 'priority', 'assignee'
    
    if (!projectId) {
      return NextResponse.json(
        { success: false, error: 'Project ID is required' },
        { status: 400 }
      )
    }
    
    // Build query
    let query = supabaseAdmin
      .from('Task')
      .select(`
        *,
        assignee: User!assigneeId(id, name, email, avatarUrl),
        reporter: User!reporterId(id, name, email),
        comments: TaskComment(count),
        timeEntries: TimeEntry(hours)
      `, { count: 'exact' })
      .eq('projectId', projectId)
    
    // Apply search filter
    if (search) {
      query = query.or(`title.ilike.%${search}%,description.ilike.%${search}%`)
    }
    
    // Apply status filter
    if (status) {
      query = query.eq('status', status)
    }
    
    // Apply priority filter
    if (priority) {
      query = query.eq('priority', priority)
    }
    
    // Apply assignee filter
    if (assigneeId) {
      if (assigneeId === 'unassigned') {
        query = query.is('assigneeId', null)
      } else {
        query = query.eq('assigneeId', assigneeId)
      }
    }
    
    // Apply type filter
    if (type) {
      query = query.eq('type', type)
    }
    
    // Apply tags filter
    if (tags.length > 0) {
      query = query.overlaps('tags', tags)
    }
    
    // Apply sorting
    query = query.order(sortBy, { ascending: sortOrder === 'asc' })
    
    // Apply pagination
    const offset = (page - 1) * limit
    query = query.range(offset, offset + limit - 1)
    
    const { data: tasks, error, count } = await query
    
    if (error) throw error
    
    // Transform tasks with computed fields
    const transformedTasks = tasks?.map(task => ({
      ...task,
      trackedHours: task.timeEntries?.reduce((sum: number, entry: any) => sum + (entry.hours || 0), 0) || 0,
      commentCount: task.comments?.[0]?.count || 0,
      comments: undefined,
      timeEntries: undefined,
    }))
    
    // Group tasks if requested
    let groupedData: any = null
    if (groupBy && transformedTasks) {
      groupedData = transformedTasks.reduce((acc: Record<string, any[]>, task) => {
        const key = task[groupBy] || 'unassigned'
        if (!acc[key]) acc[key] = []
        acc[key].push(task)
        return acc
      }, {})
    }
    
    return NextResponse.json({
      success: true,
      data: {
        tasks: transformedTasks || [],
        grouped: groupedData,
        pagination: {
          page,
          limit,
          total: count || 0,
          totalPages: Math.ceil((count || 0) / limit),
        },
      },
    })
  } catch (error: any) {
    console.error('GET /api/v1/projects/[projectId]/tasks error:', error)
    return NextResponse.json(
      { success: false, error: error.message || 'Failed to fetch tasks' },
      { status: 500 }
    )
  }
}

// POST - Create new task
export async function POST(request: NextRequest, { params }: RouteParams) {
  try {
    const { projectId } = await params
    const body = await request.json()
    
    const {
      title,
      description = '',
      type = 'task',
      status = 'todo',
      priority = 'medium',
      assigneeId,
      reporterId,
      startDate,
      dueDate,
      estimatedHours,
      tags = [],
      parentTaskId,
      milestoneId,
      sprintId,
      position,
      checklist = [],
      // AI-related fields
      aiGenerated = false,
      aiEstimatedHours,
      aiSuggestedAssignee,
      aiGeneratedDescription,
      aiTags = [],
    } = body
    
    if (!projectId) {
      return NextResponse.json(
        { success: false, error: 'Project ID is required' },
        { status: 400 }
      )
    }
    
    if (!title?.trim()) {
      return NextResponse.json(
        { success: false, error: 'Task title is required' },
        { status: 400 }
      )
    }
    
    if (!reporterId) {
      return NextResponse.json(
        { success: false, error: 'Reporter ID is required' },
        { status: 400 }
      )
    }
    
    // Get the max position for this project/status if not provided
    let taskPosition = position
    if (taskPosition === undefined) {
      const { data: maxPositionTask } = await supabaseAdmin
        .from('Task')
        .select('position')
        .eq('projectId', projectId)
        .eq('status', status)
        .order('position', { ascending: false })
        .limit(1)
        .single()
      
      taskPosition = (maxPositionTask?.position || 0) + 1
    }
    
    // Create task
    const { data: task, error } = await supabaseAdmin
      .from('Task')
      .insert({
        projectId,
        title: title.trim(),
        description: description?.trim() || '',
        type,
        status,
        priority,
        assigneeId: assigneeId || null,
        reporterId,
        startDate: startDate || null,
        dueDate: dueDate || null,
        estimatedHours: estimatedHours || null,
        trackedHours: 0,
        progress: 0,
        position: taskPosition,
        tags,
        parentTaskId: parentTaskId || null,
        milestoneId: milestoneId || null,
        sprintId: sprintId || null,
        checklist,
        checklistProgress: 0,
        attachmentCount: 0,
        commentCount: 0,
        dependencies: [],
        blockedBy: [],
        blocking: [],
        watchers: [reporterId],
        // AI fields
        aiGenerated,
        aiEstimatedHours: aiEstimatedHours || null,
        aiSuggestedAssignee: aiSuggestedAssignee || null,
        aiGeneratedDescription: aiGeneratedDescription || null,
        aiTags,
      })
      .select(`
        *,
        assignee: User!assigneeId(id, name, email, avatarUrl),
        reporter: User!reporterId(id, name, email)
      `)
      .single()
    
    if (error) throw error
    
    // Update project task count
    await supabaseAdmin.rpc('update_project_task_count', { p_project_id: projectId })
    
    return NextResponse.json({
      success: true,
      data: task,
    })
  } catch (error: any) {
    console.error('POST /api/v1/projects/[projectId]/tasks error:', error)
    return NextResponse.json(
      { success: false, error: error.message || 'Failed to create task' },
      { status: 500 }
    )
  }
}
